@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;700&display=swap);
/* Eric Meyer's Reset CSS v2.0 - http://cssreset.com */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
  outline: none;
}

* {
  box-sizing: border-box;
}

/* *** content *** */
:root {
  --black: #212121;
  --lemon: #b7d333;
}

body {
  font-family: "Cairo", sans-serif;
  line-height: 1.5em;
}

b {
  font-weight: 700;
}

.container {
  padding: 0 14%;
  max-width: 100%;
}

.header {
  padding: 0.5% 2% 0 17%;
}

.header > img {
  width: 18%;
  min-width: 173px;
}

.home-para {
  padding: 3% 20%;
}

.clearfix {
  clear: both;
}

.footer-wrapper {
  border-top: 1px solid black;
  width: 70%;
  margin: 0 auto;
  padding: 2% 3%;
  text-align: center;
}

.footer-wrapper img {
  width: 20%;
  min-width: 172px;
  margin-bottom: 1.5%;
}

/* *** thumbnails *** */

.thumbnail-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 2% 4%;
}

.thumbnail-wrapper > div {
  position: relative;
  float: left;
  margin: 0.3em;
  border-radius: 24px;
  overflow: hidden;
  max-width: 205px;
}

.thumbnail-text {
  text-align: center;
  font-size: 1.5em;
  text-transform: capitalize;
  z-index: 20;
  position: absolute;
  color: #fff;
  bottom: 23%;
  left: 0;
  right: 0;
}

.thumbnail-wrapper > div:after {
  content: " ";
  z-index: 10;
  display: block;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 110, 125, 0.7);
}

.thumbnail {
  width: 16%;
  padding: 1%;
  font-weight: bold;
  /* color: #fff; */
  height: 365px;
  border-radius: 30px;
}

.thumbnail-wrapper > div:hover:after {
  background: rgba(0, 0, 0, 0.6);
  transition-timing-function: ease-out;
  transition: all 0.5s;
}

.thumbnail-wrapper > div:hover {
  transform: translate(0, -6px);
}

/* *** accordions *** */

.accordion-wrapper {
  display: block;
  margin: 2% auto;
  width: 75%;
}

.accordion-wrapper {
  padding: 2% 5%;
}

.child-accordion-wrapper {
  padding: 2% 5%;
  background-color: #fff;
  color: #212121;
}

.accordion-title {
  background: #212121;
  color: #fff;
  margin: 1% auto;
  width: 100%;
  display: block;
}

.Collapsible .is-open {
  background: #b7d333;
  background: var(--lemon);
  width: 100%;
  display: block;
  padding: 3%;
}

.details {
  padding: 2% 0;
}

.Collapsible {
  background-color: white;
}

.Collapsible__trigger {
  display: block;
  width: 100%;
  height: 100%;
}

.Collapsible__trigger:hover {
  transform: translate(-7px, 0px);
  transition: all ease-in-out 0.1s;
}

.Collapsible__contentInner {
  border-top: 0;
}
.Collapsible__contentInner p {
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 20px;
}

.Collapsible__contentInner > img {
  width: 100%;
}

.img-overlay {
  position: relative;
}

.img-overlay:after {
  content: " ";
  z-index: 10;
  display: block;
  position: absolute;
  height: 99%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
}

.Collapsible__contentInner p:last-child {
  margin-bottom: 0;
}

.Collapsible__trigger {
  display: block;
  font-weight: bold;
  font-size: 2em;
  text-transform: capitalize;
  color: #333333;
  position: relative;
  padding: 10px;
  background: #212121;
  padding: 3% 5%;
  color: white;
}

.Collapsible__trigger:after {
  /* content: url("../images/chevron-left-solid.svg"); */
  content: url(data:image/png;base64,);
  color: #fff;
  position: absolute;
  right: 1em;
  top: 40%;
  display: block;
  transition: transform 300ms;
  transform: rotateZ(180deg);
}
.Collapsible__trigger.is-open:after {
  transform: rotateZ(-90deg);
  top: 0.8em;
}
.Collapsible__trigger.is-disabled {
  opacity: 0.5;
  background-color: grey;
}

.CustomTriggerCSS {
  background-color: lightcoral;
  transition: background-color 200ms ease;
}

.CustomTriggerCSS--open {
  background-color: darkslateblue;
}

.Collapsible__custom-sibling {
  padding: 5px;
  font-size: 12px;
  background-color: #cbb700;
  color: black;
}

@media (orientation: portrait) {
  .container {
    padding: 2% 4%;
  }
  .accordion-wrapper {
    width: 100%;
  }
  .thumbnail {
    height: 274px;
  }
  .thumbnail-text {
    font-size: 1em;
  }
  .home-para {
    padding: 3% 6%;
  }
}

@media screen and (max-width: 1000px) {
  .container {
    padding: 2% 4%;
  }
  .accordion-wrapper {
    width: 100%;
  }
  .home-para {
    padding: 3% 6%;
  }
  .Collapsible__trigger {
    font-size: 1.5em;
  }
}

@media screen and (max-width: 800px) {
  .container {
    padding: 2% 4%;
  }
  .accordion-wrapper {
    width: 100%;
  }
  .thumbnail {
    height: 274px;
  }
  .thumbnail-text {
    font-size: 1em;
  }
  .home-para {
    padding: 3% 6%;
  }
}

@media screen and (max-width: 610px) {
  .thumbnail {
    height: 177px;
  }
  .Collapsible__trigger {
    font-size: 1.1em;
  }
  .header > img,
  .footer-wrapper img {
    width: 18%;
    min-width: 137px;
  }
}

@media screen and (max-width: 500px) {
  .thumbnail-wrapper {
    display: none;
  }
}

